@import '../../components/sso/newvault/styles/variables';
@import '../../components/sso/newvault/styles/mixins';
@import '../../components/sso/newvault/styles/utility';
@import '../../components/sso/newvault/styles/grid';
@import '../../components/sso/newvault/styles/typography';
@import '../../components/sso/newvault/styles/buttons';
@import '../../components/sso/newvault/styles/tile';


/**
  Overrides
 */
#inContextTutorialLogoutPromptDialog {
  top: 10px;
  right: 10px;
  left: auto;
  bottom: auto;
  background-color: $red;
  background: $red;
  width: 390px;
  height: 160px;
  color: $white;

  span {
    font-size: $font-size-xs;
  }

  .txt-semibold {
    font-size: 14px;
  }

  .lp-grid.container.intro-tutorial {
    width: 330px;
  }

  .btn-base {
    height: 36px;
    width: auto;
    text-transform: uppercase;
    font-weight: $weight-semibold;
    background-color: transparent;
    color: $white;
    border-radius: 100px;
    padding: 0 24px 0 24px;
    &.btn-primary-dark {
      background: rgba(0,0,0,.36);
      min-width: 155px;
      &:hover, &:focus {
        background: rgba(0,0,0,.5);
      }

      &:active {
        background: rgba(0,0,0,.36);
      }
    }
    &.btn-link{
      &:hover, &:focus {
        background: rgba(0,0,0,.1);
      }
    }
    &.btn-small {
      font-size: $font-size-xxxs;
    }
    & + .btn-base {
      margin-left: 4px;
    }
  }
}
/**
  End Overrides
 */
:focus {
 outline: 0;
}

.dialogContent {
  padding: 0;
}

.dialog {
  margin-top: 1px;
}

.tile .row .text {
  width: auto;
}

.btn-row {
  min-height: 30px;
}
