/* TYPOGRAPHY */
h2, .h2{
  color: $gray-shade-4;
  font-size: $font-size-h4;
  font-weight: $weight-normal;
  text-align: left;
}

a {
  color: #3498DB;
  font-weight: $weight-semibold;
}

.txt-sm {
  @include font-sm();
  line-height: 18px;
}
.txt-md {
  @include font-md();
  line-height: 19px;
}
.txt-lg {
  @include font-md();
  font-size: 22px;
  color: $gray-shade-5;
}

// This should go after other text styles so it properly overrides weight.

.txt-bold{
  font-weight: $weight-bold;
}
.txt-semibold{
  font-weight: $weight-semibold;
}

.txt-dark{
  color: $gray-shade-6;
}